import React, { useState } from 'react';
import { connect } from 'react-redux';
import {Form, Input, Card, Col, Row, message} from 'antd';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import CustomTab from "components/layout-components/custom.tab";
import ButtonPermission from "components/permission-components/button.permission";
import {SaveOutlined} from "@ant-design/icons";
import ApiService from "../../../../../../../../services/api.service";
import AudioForm from "../../../components/forms/audio.form";

function mapStateToProps(state) {
 return {

 };
}

function Index({...props})  {
    const [ loading, setLoading ] = useState(false)
    const [ form  ] = Form.useForm();
    const onFinish = ()=> {
        setLoading(true)
        form.validateFields()
            .then((value)=> {
                let newUrl = `/api/v1/audio`
                let newValue = {}
                Object.entries(value).map(([key,value])=> {
                    if(key === 'form_type'){
                        newUrl += '/'+value
                    }else{
                        Reflect.set(newValue,key,value)
                    }
                })
                new ApiService({
                    url:newUrl,
                    body:newValue
                }).post()
                    .then((response)=> {
                        message.info(response?.message ?? "HEHEHE")
                      form.resetFields()
                    })
                    .catch((err)=> {
                        console.log({err})
                    })

                setLoading(false)
            })
            .catch((err)=> {
                setLoading(false)
            })
    }
  return (
      <React.Fragment>
          <HeroComponents
              name={"Create"}
              positionName={['Master Data','Master Audio','Category']}
              thumbnail={null}
              extra={<>
                  <ButtonPermission
                      permission={['admin']}
                      theme={{
                          type: "notification",
                          property: {
                              message: "Access Denied",
                              description: "Anda tidak dapat menyimpan ke database "
                          }
                      }}
                      config={{
                          icon: <SaveOutlined />,
                          type: "primary",
                          danger:false,
                          loading,
                          ghost:true,
                          size: "small",
                          onClick: onFinish,
                      }}>
                      <span>Save</span>
                  </ButtonPermission>
              </>}
          />
          <div style={{marginTop:90}}>
            <ContainerTailwind>
              <Card bordered={false}>
                <Row gutter={24}>
                  <Col xs={24}>
                    <AudioForm  {...props} mode={'CREATE'} type={'category'} form={form} initialValue={{form_type:'category'}}/>
                  </Col>
                </Row>
              </Card>
            </ContainerTailwind>
          </div>
      </React.Fragment>
  );
}

export default connect(
 mapStateToProps,
)(Index);