import React, {useEffect, useState} from 'react';
import {connect} from 'react-redux';
import {Col, Form, Input, Row} from 'antd';
import ColorPicker from "../../../../../../../components/shared-components/ColorPicker";
import {COLOR_DEFAULT_SELECTION} from "../../../../../../../configs/AppConfig";

function mapStateToProps(state) {
	return {};
}

/**
 * @param {object} props
 * @param {string} props.current
 * @param {'CREATE' | 'UPDATE'} props.mode
 * @param {object} props.initialValue
 * @param {ReactNode | string} props.children
 * @param {object} props.initialValue
 * @param {function | JSX.Element} props.children
 * @returns {JSX.Element}
 * @constructor
 */
function AudioForm({...props}) {
	let {form, current, mode = 'CREATE', initialValue} = props
	const [name,setName]= useState(()=> {
		if(initialValue) return initialValue?.data?.name ?? ""
		return ""
	})
	useEffect(() => {
		if (form && form.setFieldsValue && current) {
			form.setFieldsValue({
				form_type: current
			})
		}
	}, [current])
	
	function onChangeName({target}) {
		setName(target?.value)
		let field = {
			identifier: `${target?.value}`.replace(/ /g, '-').toLowerCase(),
			data: {
				name: target.value,
			}
		}
		if(mode === 'UPDATE'){
			Reflect.deleteProperty(field,'identifier')
		}
		
		form.setFieldsValue(field)
	}
	
	function onChangeColor(val){
		form.setFieldsValue({
			data: {
				color: val?.hex
			}
		})
	}
	return (
		<Form
      form={form}
      layout={'vertical'}
      initialValue={{
        ...initialValue
      }}
    >
			<Form.Item
				hidden
				name={'identifier'} label={'Identifier'}>
				<Input/>
			</Form.Item>
			<Row gutter={24}>
				<Col xs={24} lg={18}>
					<Form.Item name={['data', 'name']} label={'Display Name'}
					           rules={[{required: true, message: "can't be empty!"}]}>
						<Input value={name} onChange={onChangeName}/>
					</Form.Item>
				</Col>
				<Col xs={24} lg={6}>
          <h4>Color</h4>
          <ColorPicker color={initialValue?.data?.color ?? COLOR_DEFAULT_SELECTION } colorChange={onChangeColor}/>
					<Form.Item hidden name={['data', 'color']} label={'Color'} initialValue={initialValue?.data?.color ?? COLOR_DEFAULT_SELECTION}>
            <Input/>
					</Form.Item>
				</Col>
			
			</Row>
			<Form.Item name={'form_type'} hidden initialValue={props?.initialValue?.form_type ?? ""}>
				<Input/>
			</Form.Item>
			{props?.children && props?.children}
		</Form>
	);
}

export default connect(
	mapStateToProps,
)(AudioForm);